<script>
	import {initializeApp} from "firebase/app"
	import { getAuth, signOut} from "firebase/auth";
	import Login from "./components/Login.svelte";
	import Dashboard from "./components/Dashboard.svelte";
	import DepositManagement from "./components/DepositManagement.svelte";
	import WithdrawalManagement from "./components/WithdrawalManagement.svelte";
	import Router, {link, push} from 'svelte-spa-router';
	import {loginState} from './store.js';
	import {user} from './store.js';
	import {app} from './store.js';

	// export let logout;
	// import DepositManagement from './pages/DepositManagement.svelte';

	const routes = {
		'/': Login,
		'/DepositManagement': DepositManagement,
		'/WithdrawalManagement': WithdrawalManagement,
		'/Dashboard': Dashboard,
	};

	// let logout = '로그아웃';

	// let loginout = '로그인'


	function loginOut(){
		
		if($user != null){
			console.log("비로그인 상태");
			push('/');
			// push('/');
			// $user = $user;
			// console.log($user)
		} else {
			console.log('로그아웃 직후', $loginState)
			$loginState = '';
			console.log("로그인 상태");
			logout();
			push('/');
			
		}
	};
	const logout = async () => {
            const auth = getAuth($app);
            signOut(auth)
            $user = null
        };
    

	// function pushLogin(){
	// 	// alert('서비스 준비 중입니다.')
	// }

	function dashboard(){
		push('/Dashboard');
		// alert('서비스 준비 중입니다.')
	};

	function pushDeposit(){
		push('/DepositManagement');
	};

	function pushWithdrawal(){
		push('/WithdrawalManagement');
	};
	
</script>

<header>
	<h1>BPG ADMIN</h1>
	<ul>
		{#if $loginState == ''}
			<div></div>
		{:else}
			<button class="btn default" on:click={loginOut}>{$loginState}</button>
		{/if}
		<button class="btn default" on:click={dashboard}>대시보드</button>
		<button class="btn default" on:click={pushDeposit}>입금 관리</button>
		<button class="btn default" on:click={pushWithdrawal}>출금 관리</button>
	</ul>
</header>

